import { Image } from '@/db/types';
import images from '../db/images';

export const LOCALSRORAGE_LENGTH = 241;

export function addInLocalStorage() : void {
  // eslint-disable-next-line max-len
  if (localStorage.length !== LOCALSRORAGE_LENGTH || localStorage.length !== LOCALSRORAGE_LENGTH + 1) {
    images.forEach((obj) => {
      localStorage.setItem(
        obj.imageNum,
        JSON.stringify({
          author: obj.author,
          name: obj.name,
          year: obj.year,
          imageNum: obj.imageNum,
          style: obj.style,
          correctArt: false,
          correctPic: false,
        }),
      );
    });
  }
}

export function takeWordsFromLocalStorage() : Image[] {
  const words = [];
  for (let i = 0; i < localStorage.length; i += 1) {
    const key = localStorage.key(i);
    if (key !== 'loglevel:webpack-dev-server') {
      if (key) {
        const wordString = localStorage.getItem(key);
        if (wordString) {
          const wordObj = JSON.parse(wordString);
          words.push(wordObj);
        }
      }
    }
  }
  return words;
}

export const changeData = (imageNum: string, typeQuiz: string, prop: boolean) : void => {
  const imageString = <string>localStorage.getItem(imageNum);
  const image = JSON.parse(imageString);
  if (typeQuiz === 'artists' && prop) {
    image.correctArt = true;
  } else if (typeQuiz === 'pictures' && prop) {
    image.correctPic = true;
  }
  localStorage.setItem(imageNum, JSON.stringify(image));
};
