<template>
  <div class="end__block">
      <h1 class="end__title">{{success? 'congratulations' : 'game over'}}</h1>
      <h2>{{correctAnswers}} / {{len}}</h2>
      <img v-if="success" src="./../assets/good.svg" alt="success" class="end__img" />
      <img v-else src="./../assets/bad.svg" alt="success" class="end__img" />
      <p v-if="!success" class="end__text">Play again?</p>
      <div v-if="success" class="end__btns">
        <router-link to="/">
          <UiBtn class="end__btn">
            <img src="./../assets/home.svg" alt="" />
            <span>Home</span>
          </UiBtn>
        </router-link>
        <router-link to="/categories">
          <UiBtn class="end__btn">
            <img src="./../assets/categories.svg" alt="" />
          <span>Categories</span>
        </UiBtn>
        </router-link>
      </div>
      <div v-else class="end__btns">
        <router-link to="/categories">
          <UiBtn class="end__btn_not-success">
            <span>YES</span>
          </UiBtn>
        </router-link>
        <router-link to="/">
          <UiBtn class="end__btn_not-success">
            <span>NO</span>
        </UiBtn>
        </router-link>
      </div>
  </div>
</template>

<script>
import UiBtn from '@/components/UI/UiBtn.vue';

export default {
  name: 'Congratulations',
  data() {
    return {
      success: false,
      correctAnswers: 0,
      len: 0,
    };
  },
  methods: {
    defineEnd() {
      this.len = this.$store.state.playPicturesFull.length;
      if (this.$store.state.typeQuiz === 'artists') {
        /* eslint-disable max-len */
        this.correctAnswers = this.$store.state.playPicturesFull.filter((el) => el.correctArt).length;
      } else {
        this.correctAnswers = this.$store.state.playPicturesFull.filter((el) => el.correctPic).length;
      }
      this.success = this.correctAnswers >= this.len / 2;
    },
  },
  beforeMount() {
    this.defineEnd();
  },
  components: {
    UiBtn,
  },
};
</script>

