<template>
  <div class="score">
    <ui-image
      v-if="$store.state.typeQuiz === 'artists'"
      :image="card"
      :class="{ 'score__img_correct': !card.correctArt, 'score__img': true }"
    />
    <ui-image
      v-else
      :image="card"
      :class="{ 'score__img_correct': !card.correctPic, 'score__img': true }"
    />
    <img
      v-if="($store.state.typeQuiz === 'artists' && card.correctArt) ||
        ($store.state.typeQuiz === 'pictures' && card.correctPic)"
