    />
    <img v-else src="./../assets/wrong.png" alt="wrong" class="score__correct" />
    <div class="score__block_text">
      <p class="score__text">{{card.author}}</p>
      <p class="score__text">{{card.name}}</p>
      <p class="score__text">{{card.year}}</p>
    </div>
  </div>
</template>

