import { createStore } from 'vuex';
import images from '@/db/images';
import { Image } from '@/db/types';
// import { takeWordsFromLocalStorage } from '@/storage';

export default createStore({
  state: {
    typeQuiz: 'artists',
    playPictures: [],
    playPicturesFull: [],
    answers: [],
    images,
  },
  mutations: {
    SET_TYPEQUIZ: (state, payload) => {
      state.typeQuiz = payload;
    },
    SET_PLAYPICS: (state, payload) => {
      state.playPictures = payload;
    },
    SET_PLAYPICS_FULL: (state, payload) => {
      state.playPicturesFull = payload;
    },
    SET_ANSWERS: (state, payload) => {
      state.answers = payload;
    },
    SET_CORRECT_TO_IMG: (state, payload) => {
      const correctImage = <Image>state.images.find((elem) => elem.imageNum === payload);
      if (state.typeQuiz === 'artists') {
        correctImage.correctArt = true;
      } else {
        correctImage.correctPic = true;
      }
    },
    SET_WRONG_TO_IMG: (state, payload) => {
      const correctImage = <Image>state.images.find((elem) => elem.imageNum === payload);
      if (state.typeQuiz === 'artists') {
        correctImage.correctArt = false;
      } else {
        correctImage.correctPic = false;
      }
    },
  },
  actions: {
    SAVE_TYPEQUIZ: (context, payload) => {
      context.commit('SET_TYPEQUIZ', payload);
    },
    SAVE_PLAYPICS: (context, payload) => {
      context.commit('SET_PLAYPICS', payload);
    },
    SAVE_PLAYPICS_FULL: (context, payload) => {
      context.commit('SET_PLAYPICS_FULL', payload);
    },
    SAVE_ANSWERS: (context, payload) => {
      context.commit('SET_ANSWERS', payload);
    },
    SAVE_CORRECT_TO_IMG: (context, payload) => {
      context.commit('SET_CORRECT_TO_IMG', payload);
    },
    SAVE_WRONG_TO_IMG: (context, payload) => {
      context.commit('SET_WRONG_TO_IMG', payload);
    },
  },
  modules: {
  },
});
