<template>
  <div class="play__btns">
    <router-link to="/">
      <UiBtn class="play__btn">
        <img src="./../assets/home.svg" alt="" />
        <span>Home</span>
      </UiBtn>
    </router-link>
    <router-link to="/categories">
      <UiBtn class="play__btn">
        <img src="./../assets/categories.svg" alt="" />
        <span>Categories</span>
      </UiBtn>
    </router-link>
  </div>
  <div class="page__play">
    <Question :playMode="this.$store.state.playPictures[0].author" />
    <UiImage
      v-if="$store.state.typeQuiz === 'artists'"
      :image="$store.state.playPictures[0]"
      class="play__img"
    />
    <Answers
      :pictures="$store.state.answers"
    />
  </div>
</template>

<script>
import Question from '@/components/Question.vue';
import UiImage from '../components/UI/UiImage.vue';
import UiBtn from '../components/UI/UiBtn.vue';
import Answers from '../components/Answers.vue';
import createRandomAns from '../service/createRandomAns';

export default {
  components: {
    Question,
    UiImage,
    Answers,
    UiBtn,
  },
  methods: {
    createRandomAnswers() {
      const artistsTotal = createRandomAns(this.$store.state.playPicturesFull,
        this.$store.state.playPictures[0], this.$store.state.typeQuiz);
      this.$store.dispatch('SAVE_ANSWERS', artistsTotal);
    },
  },
  beforeMount() {
    this.createRandomAnswers();
  },
};
</script>

