<template>
  <div
    v-if="$store.state.typeQuiz === 'artists'"
    :class="{ answer__active: isActive, 'answer__text': true }"
    @click="makeActive"
  >
    {{picture}}
  </div>
  <div
    v-else
    class="answer__div_img"
    @click="makeActive"
  >
    <UiImage
      :image="imageObj"
      class="answer__img"
    />
  </div>
  <Modal v-model:show="modalVisible">
    <CorrectAnswer
      :correct="correct"
      @update="hideDialog"
    />
  </Modal>
</template>

<script>
import Modal from '@/components/UI/Modal.vue';
import UiImage from '@/components/UI/UiImage.vue';
import CorrectAnswer from '@/components/CorrectAnswer.vue';
import { changeData } from '@/storage';

export default {
  name: 'Answer',
  props: {
    picture: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      isActive: false,
      modalVisible: false,
      correct: false,
      imageObj: {
        name: '',
        imageNum: this.picture,
      },
    };
  },
  methods: {
    checkAnswer() {
      if (this.$store.state.typeQuiz === 'artists') {
        this.correct = (this.$store.state.playPictures[0].author === this.picture);
      } else {
        this.correct = (this.$store.state.playPictures[0].imageNum === this.picture);
      }
      if (this.correct) {
        this.$store.dispatch('SAVE_CORRECT_TO_IMG', this.$store.state.playPictures[0].imageNum);
      } else {
        this.$store.dispatch('SAVE_WRONG_TO_IMG', this.$store.state.playPictures[0].imageNum);
      }
      // changeData(this.imageObj.imageNum, this.$store.state.typeQuiz, this.correct);
    },
    makeActive() {
      this.isActive = true;
      this.checkAnswer();
      this.modalVisible = true;
    },
    hideDialog() {
      this.modalVisible = false;
      this.isActive = false;
    },
  },
  components: {
    Modal,
    CorrectAnswer,
    UiImage,
  },
};
</script>

