<template>
  <div class="answer__block">
    <div>
      <img v-if="correct" src="./../assets/correct.png" alt="correct" class="answer__img" />
      <img v-else src="./../assets/wrong.png" alt="wrong" class="answer__img" />
    </div>
    <ui-image :image="$store.state.playPictures[0]" class="answer__pic" />
    <p class="answer__text">{{$store.state.playPictures[0].author}}</p>
    <p class="answer__text">{{$store.state.playPictures[0].name}}</p>
    <p class="answer__text">{{$store.state.playPictures[0].year}}</p>
    <UiBtn
      @click="openNextQuestion"
      class="answer__btn"
    >
      Next
    </UiBtn>
  </div>
</template>

<script>
import UiImage from '@/components/UI/UiImage.vue';
import UiBtn from '@/components/UI/UiBtn.vue';
import createRandomAns from '../service/createRandomAns';

export default {
  name: 'CorrectAnswer',
  props: {
    correct: {
      type: Boolean,
      required: true,
    },
  },
  components: {
    UiImage,
    UiBtn,
  },
  methods: {
    openNextQuestion() {
      if (this.$store.state.playPictures.length !== 1) {
        this.$store.dispatch('SAVE_PLAYPICS', this.$store.state.playPictures.slice(1));
        const answersTotal = createRandomAns(this.$store.state.playPicturesFull,
          this.$store.state.playPictures[0], this.$store.state.typeQuiz);
        this.$store.dispatch('SAVE_ANSWERS', answersTotal);
      } else {
        this.$router.push('end');
      }
      this.$emit('update');
    },
  },
};
</script>

<style lang="scss" scoped>
