  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.answer__img {
  width: 80px;
  margin-bottom: 10px;
}

.answer__pic {
  width: 70%;
}

.answer__text {
  color: rgb(105, 105, 105);
  text-transform: uppercase;
  margin: 10px;
}

.answer__btn {
  justify-content: center;
}
</style>
