<template>
  <h1 class="score__title">{{category[0].style.toUpperCase()}}</h1>
  <div class="play__btns">
    <router-link to="/">
      <UiBtn class="play__btn">
        <img src="./../assets/home.svg" alt="" />
        <span>Home</span>
      </UiBtn>
    </router-link>
    <router-link to="/categories">
      <UiBtn class="play__btn">
        <img src="./../assets/categories.svg" alt="" />
        <span>Categories</span>
      </UiBtn>
    </router-link>
  </div>
  <div class="page__score">
    <ScoreImg
      v-for="card in category"
      :card="card"
      v-bind:key="card.name"
    />
  </div>
</template>

<script>
import ScoreImg from '@/components/ScoreImg.vue';
import UiBtn from '@/components/UI/UiBtn.vue';
import images from '../db/images';

export default {
  name: 'Score',
  data() {
    return {
      category: [],
    };
  },
  methods: {
    findCategory() {
      const categoryName = this.$route.params.id;
      this.category = images.filter((pic) => pic.style === categoryName);
    },
  },
  beforeMount() {
    this.findCategory();
  },
  components: {
    ScoreImg,
    UiBtn,
  },
};
</script>

