<template>
  <div class="category">
  <router-link to="/play"
    @click="this.$store.dispatch('SAVE_PLAYPICS', category);
    this.$store.dispatch('SAVE_PLAYPICS_FULL', category);"
  >
    <div class="category__header">
      <p>{{category[0].style}}</p>
      <p>{{category.length}}</p>
    </div>
    <ui-image
      :image="category[0]"
      :class="{ 'category__not-finished': !isFinished, 'category__img': true }"
    />
    </router-link>
    <div
      v-if="($store.state.typeQuiz === 'artists'
      && category.filter(elem => elem.correctArt).length) ||
        category.filter(elem => elem.correctPic).length"
      class="score"
      @click="openScore"
    >
      <img src="./../assets/score.svg" alt="" />
      <span>Score</span>
      <span>{{$store.state.typeQuiz === 'artists' ?
        category.filter(elem => elem.correctArt).length
        : category.filter(elem => elem.correctPic).length}}
      </span>
    </div>
  </div>
</template>

<script>
import UiImage from './UI/UiImage.vue';

export default {
  name: 'Category',
  props: {
    category: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      isFinished: false,
      path: '',
    };
  },
  components: {
    UiImage,
  },
  methods: {
    checkIsFinished() {
      // this.isFinished = this.category.filter((elem) => elem.correct).length >= 1;
      const len = this.$store.state.typeQuiz === 'artists'
        ? this.category.filter((elem) => elem.correctArt).length
        : this.category.filter((elem) => elem.correctPic).length;
      this.isFinished = len >= 1;
    },
    openScore() {
      this.$router.push(`/score/${this.category[0].style}`);
    },
  },
  beforeMount() {
    this.checkIsFinished();
    this.path = this.$store.state.typeQuiz;
  },
};
</script>

