  position: relative;
  margin: 20px;
  width: 250px;
  height: 330px;
}

a {
  display: flex;
  flex-direction: column;
  background: rgb(123, 106, 88);
  border-top-left-radius: 40px;
  text-decoration: none;
}

.category__header {
  height: 80px;
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.category__not-finished {
  filter: grayscale(100%);
}

.score {
  position: absolute;
  color: #fff;
  font-size: 24px;
  bottom: -20px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  width: 100%;
  height: 60px;
  background: rgb(123, 106, 88);
  z-index: 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  cursor: pointer;
}

.disabled {
  pointer-events: none;
}
</style>
