import { Image } from '@/db/types';

const createRandomAns = (arr: Image[], correctAnswer: Image, typeQuiz: string) : string[] => {
  const answers = [];
  const arrAuthorUnique = <Array<string>>[];
  let correct = '';
  if (typeQuiz === 'artists') {
    arr.forEach((elem) => answers.push(elem.author));
    correct = correctAnswer.author;
  } else {
    arrAuthorUnique.push(correctAnswer.author);
    arr.forEach((elem) => {
      if (!arrAuthorUnique.includes(elem.author)) {
        arrAuthorUnique.push(elem.author);
        answers.push(elem.imageNum);
      }
      correct = correctAnswer.imageNum;
    });
  }
  answers.unshift(correct);
  const answersSet = new Set(answers);
  const answersNew = [...answersSet];
  const answersNewFirst = [answersNew[0]];
  const answersNewRest = answersNew.slice(1).sort(() => Math.random() - 0.5).slice(0, 3);
  const answersTotal = answersNewFirst.concat(answersNewRest);
  answersTotal.sort(() => Math.random() - 0.5);
  return answersTotal;
};

export default createRandomAns;
